import React, { Component} from "react"

import Slider from "react-slick"

import "./slick.scss";

import teamData from "../teamData"
import servicesData from "../servicesData"

import teamControlLeft from "../../assets/team-control-left.svg";
import teamControlRight from "../../assets/team-control-right.svg";

const PrevArrow = (props) => {
    return (
      <div {...props} className="team-controls__item team-controls__item_prev" style={{display: 'inline-flex'}}>
        <img className="team-controls__icon" src={teamControlLeft} alt="Arrow Left"/>
      </div>
    );
}
  
const NextArrow = (props) => {
  return (
    <div {...props} className="team-controls__item team-controls__item_next" style={{display: 'inline-flex'}}>
      <img className="team-controls__icon" src={teamControlRight} alt="Arrow Right"/>
    </div>
  );
}
  
const options1 = {
  fade: true,
  infinite: true,
  speed: 500,
  slidesToShow: 1,
  slidesToScroll: 1,
  prevArrow: <PrevArrow />,
  nextArrow: <NextArrow />
}

const options2 = {
  fade: true,
  infinite: true,
  speed: 500,
  slidesToShow: 1,
  slidesToScroll: 1,
  arrows: false
}

const options3 = {
  fade: true,
  infinite: true,
  speed: 500,
  slidesToShow: 2,
  slidesToScroll: 2,
  arrows: false
}

export default class Team extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nav1: null,
      nav2: null,
      nav3: null
    };
  }

  componentDidMount() {
    this.setState({
      nav1: this.slider1,
      nav2: this.slider2,
      nav3: this.slider3
    });
  }

  render() {
    return (
    <section className="team">
      <div className="team__wrapper container">
        <h4 className="g-subtitle">OUR TEAM</h4>

        <div className="team__content">
          <div>
            <div className="team-person">


              <Slider asNavFor={this.state.nav2} ref={slider => (this.slider1 = slider)} {...options1}>
              {teamData.map(({id, photo, name, position}) => 
                <div className="team-person__item" key={id}  style={{width: 'auto'}}>
                  <div className="team-person__photo">
                    <img className="team-person__source" src={photo} alt="Team Member"/>
                  </div>
    
                  <h5 className="team-person__name">{name}</h5>
                  <p className="team-person__position">{position}</p>
                </div>
              )}

              </Slider>
            </div>
          </div>
  
          <div>
            <div className="team-about">
              <h5 className="team-about__title">About</h5>
              <Slider asNavFor={this.state.nav3} ref={slider => (this.slider2 = slider)} {...options2}>
              {teamData.map(({id, description}) => 
                  <p className="team-about__text" key={id}>{description}</p>
              )}
              </Slider>
            </div>
    
            <div className="team-services">
              <h5 className="team-services__title">Services</h5>

              <div className="team-services__content">
              <Slider asNavFor={this.state.nav1} ref={slider => (this.slider3 = slider)} {...options3}>
              {servicesData.map(({id, icon, title}) => 
                <div className="team-services__item" key={id}>
                  <img className="team-services__icon" src={icon} alt="Service Icon" />
                  <h5 className="team-services__desc">{title}</h5>
                </div>
              )}
              </Slider>
              </div>
            </div>
          </div>
        </div>

      </div>
    </section>
    );
  }
}